<template>
  <div class="container">
    <h2 class="text-center">系統公告</h2>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.push('/')" v-if="this.$store.state.account.ident === ''">
      返回登入
    </button>
    <button class="btn btn-outline-secondary btn-sm mb-3" type="button" @click="$router.push('/examLobby')" v-if="this.$store.state.account.ident !== ''">
      返回大廳
    </button>
    <div class="row">
      <p v-if="systemNoticeData.length === 0" class="text-danger fw-bold fs-5 text-center">目前尚未有使用紀錄。</p>
      <div v-else v-for="item in systemNoticeData" class="col-12">
        <div class="card">
          <div class="card-body">
            <h5 class="card-title">{{ item.title }}</h5>
            <h6 class="card-subtitle mb-2 text-muted">{{ item.time }}</h6>
            <p class="card-text">{{ item.content }}</p>
          </div>
        </div>
      </div>
      <div class="col-12"></div>
      <div class="col-12"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "SystemNotice",
  components: {},
  data() {
    return {
      systemNoticeData: [
        {
          title: "公告標題01",
          time: "113/05/18 08:49",
          content: "此網頁目前還在施工中。",
        },
      ],
    };
  },
  methods: {},
  created() {
    this.$http.get("api/systemManage/getSystemNotice").then((res) => {
      if (res.data.code === 200) {
        this.systemNoticeData = res.data.data;
      } else {
        console.log(res.data);
      }
    });
  },
  mounted() {},
  computed: {},
  watch: {},
};
</script>

<style lang="scss"></style>
